<template>
  <!--  -->
  <div class="link-tools-contain">
    <el-dialog :class="activeName == 'first' ? 'min-1051' : 'min-720'" title="" :visible.sync="mylinkToolsVisible" :before-close="handleClose">
      <!--  -->
      <div class="links-types">
        <label class="types-title">:</label>
        <el-select v-model="linksType" placeholder="" @change="linksTypeChange">
          <el-option v-for="item in linksTypeOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
        </el-select>
        <el-select v-model="twoLevelValue" placeholder="" v-if="twoLevelVisible" @change="twoLevelValueChange">
          <el-option v-for="item in twoLevelOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
        </el-select>
        <el-select v-model="threeLevelValue" placeholder="" v-if="threeLevelVisible" @change="threeLevelValueChange">
          <el-option v-for="item in threeLevelOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
        </el-select>
        <!--  -->
        <div class="goods-category">
          <treeselect v-model="goodsCategory" :options="goodsCategoryOptions" :normalizer="normalizer" :default-expand-level="1" placeholder="" noOptionsText="" noResultsText="" @select="goodsCategorySelect" />
        </div>
      </div>
      <!-- h5 -->
      <div class="h5link-contain" v-if="h5linkVisible">
        <div class="h5link-row">
          <label class="h5link-title">:</label>
          <el-input v-model="h5linkObj.name" placeholder=""></el-input>
        </div>
        <div class="h5link-row">
          <label class="h5link-title">:</label>
          <el-input v-model="h5linkObj.url" placeholder=""></el-input>
        </div>
        <div class="h5link-row">
          <label class="h5link-title">:</label>
          <el-input v-model="h5linkObj.fixParam" type="textarea" resize="none" :rows="4" placeholder="()"></el-input>
        </div>
        <div class="h5link-row">
          <label class="h5link-title">:</label>
          <!-- <el-input v-model="h5linkObj.dynamicPara" placeholder="()"></el-input> -->
          <el-select v-model="h5linkObj.dynamicPara" multiple placeholder="">
            <el-option v-for="item in dynamicParaOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </div>
      </div>
      <!--  -->
      <div class="goods-links-contian">
        <div class="links-tools-row">
          <treeselect :value="category" :options="categoryOptions" :normalizer="normalizer" :default-expand-level="1" placeholder="" noOptionsText="" noResultsText="" @select="categorySelect" />

          <treeselect v-model="classify" :options="classifyOptions" :normalizer="normalizer" :default-expand-level="1" placeholder="" noOptionsText="" noResultsText="" @select="classifySelect" />
          <el-input class="links-tools-input" placeholder="/" v-model="searchGoods" @keyup.native="value => toSearchInput(value)" clearable @clear="clearInput">
            <i slot="prefix" class="el-input__icon el-icon-search"></i>
          </el-input>
        </div>
        <!--  -->
        <div class="goods-link-content">
          <ul class="goods-link-list">
            <li class="goods-link-item" v-for="(item, index) in goodsList" @click="selectLinkItem(item)" :key="index">
              <img :src="item.src" alt="" />
              <div class="inline-block goods-message">
                <p class="limit-2">{{ item.name }}</p>
                <p class="gray-color">{{ item.goodsNum }}</p>
                <span>{{ item.price }}</span>
              </div>
            </li>
          </ul>
        </div>
        <!--  -->
        <div class="links-tools-page">
          <el-pagination @size-change="linkSizeChange" @current-change="linkCurrentChange" :current-page="currentPage" :page-sizes="[20, 30, 40, 50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>
        </div>
      </div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <!--  -->
          <div class="links-tools-row">
            <treeselect :value="category" :options="categoryOptions" :normalizer="normalizer" :default-expand-level="1" placeholder="" noOptionsText="" noResultsText="" @select="categorySelect" />

            <treeselect v-model="classify" :options="classifyOptions" :normalizer="normalizer" :default-expand-level="1" placeholder="" noOptionsText="" noResultsText="" @select="classifySelect" />
            <el-input class="links-tools-input" placeholder="/" v-model="searchGoods" @keyup.native="value => toSearchInput(value)" clearable @clear="clearInput">
              <i slot="prefix" class="el-input__icon el-icon-search"></i>
            </el-input>
          </div>
          <!--  -->
          <div class="goods-link-content">
            <ul class="goods-link-list">
              <li class="goods-link-item" v-for="(item, index) in goodsList" @click="selectLinkItem(item)" :key="index">
                <img :src="item.src" alt="" />
                <div class="inline-block goods-message">
                  <p class="limit-2">{{ item.name }}</p>
                  <p class="gray-color">{{ item.goodsNum }}</p>
                  <span>{{ item.price }}</span>
                </div>
              </li>
            </ul>
          </div>
          <!--  -->
          <div class="links-tools-page">
            <el-pagination @size-change="linkSizeChange" @current-change="linkCurrentChange" :current-page="currentPage" :page-sizes="[20, 30, 40, 50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>
          </div>
        </el-tab-pane>

        <el-tab-pane label="" name="second">
          <!--  -->
          <div class="goods-category">
            <treeselect v-model="goodsCategory" :options="goodsCategoryOptions" :normalizer="normalizer" :default-expand-level="1" placeholder="" noOptionsText="" noResultsText="" @select="goodsCategorySelect" />
          </div>
        </el-tab-pane>

        <el-tab-pane label="" name="third">
          <!--  -->
          <div class="main-function-page">
            <el-tree :data="mainFunctionPage" :expand-on-click-node="false" :props="defaultMainPageProps" @node-click="mainPageNodeClick"></el-tree>
          </div>
        </el-tab-pane>

        <el-tab-pane label="" name="fourth">
          <!--  -->
          <div class="definepage">
            <ul class="definepage-ul">
              <li class="definepage-ul-li" v-for="(item, index) in definePageList" @click="definePageClick(item)" :key="index">{{ item.name }}</li>
            </ul>
          </div>
        </el-tab-pane>
      </el-tabs>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelLinkSelect"> </el-button>
        <el-button type="primary" @click="confirmLinkSelect"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 
// import the component
import Treeselect from '@riophae/vue-treeselect';
import { log } from '@/utils/index.js';
export default {
  name: 'links',
  props: ['linkToolsVisible'],
  data() {
    return {
      // ---------------------------
      twoLevelVisible: true,
      threeLevelVisible: true,
      // 
      linksType: '',
      linksTypeOptions: [
        {
          id: '1',
          value: 'shoplink',
          label: ''
        },
        {
          id: '2',
          value: 'memberlink',
          label: ''
        },
        {
          id: '3',
          value: 'definelink',
          label: ''
        },
        {
          id: '4',
          value: 'h5link',
          label: 'H5'
        },
        {
          id: '5',
          value: 'otherlink',
          label: ''
        }
      ],
      // 
      twoLevelValue: '',
      twoLevelOptions: [
        {
          id: '1',
          value: 'shoplink',
          label: ''
        }
      ],
      // 
      threeLevelValue: '',
      threeLevelOptions: [
        {
          id: '1',
          value: 'shoplink',
          label: ''
        }
      ],

      // h5link
      h5linkVisible: false,
      h5linkObj: {
        name: '',
        url: '',
        fixParam: '',
        dynamicPara: '',
        dynamicParaOptions: []
      },
      // ---------------------------
      activeName: 'first',
      mylinkToolsVisible: this.linkToolsVisible,
      // //
      selectType: 1, // 1: ; 2:; 3/4:
      // 
      searchGoods: '',
      category: null,
      categoryOptions: [
        {
          key: 'a',
          name: 'a',
          subOptions: [
            {
              key: 'aa',
              name: 'aa'
            }
          ]
        }
      ],
      normalizer(node /*, id */) {
        // there is an extra `id` argument,
        // which could be useful if you have multiple instances
        // of vue-treeselect that share the same `normalizer` function
        return {
          id: node.key,
          label: node.name,
          children: node.subOptions
        };
      },
      goodsList: [
        {
          id: '111',
          name: 'Gaming K70 RGB Rapidfire',
          goodsNum: 'errerr',
          src: 'https://pic01-10001430.image.myqcloud.com/adb6387e-a3f4-4b7f-a7dd-1b7fe242f9f7',
          price: 567
        },
        {
          id: '112',
          name: 'Gaming K70 RGB Rapidfire',
          goodsNum: 'errerr',
          src: 'https://pic01-10001430.image.myqcloud.com/adb6387e-a3f4-4b7f-a7dd-1b7fe242f9f7',
          price: 567
        },
        {
          id: '113',
          name: 'Gaming K70 RGB Rapidfire',
          goodsNum: 'errerr',
          src: 'https://pic01-10001430.image.myqcloud.com/adb6387e-a3f4-4b7f-a7dd-1b7fe242f9f7',
          price: 567
        },
        {
          id: '112',
          name: 'Gaming K70 RGB Rapidfire',
          goodsNum: 'errerr',
          src: 'https://pic01-10001430.image.myqcloud.com/adb6387e-a3f4-4b7f-a7dd-1b7fe242f9f7',
          price: 567
        },
        {
          id: '113',
          name: 'Gaming K70 RGB Rapidfire',
          goodsNum: 'errerr',
          src: 'https://pic01-10001430.image.myqcloud.com/adb6387e-a3f4-4b7f-a7dd-1b7fe242f9f7',
          price: 567
        }
      ],
      // 
      currentPage: 1, // 
      pageSize: 20, // 
      total: 0, // 

      // 
      classify: null,
      classifyOptions: [
        {
          key: 'b',
          name: 'b',
          subOptions: [
            {
              key: 'bb',
              name: 'bb'
            }
          ]
        }
      ],

      // 
      goodsCategory: null,
      goodsCategoryOptions: [
        {
          key: 'a',
          name: 'a',
          subOptions: [
            {
              key: 'aa',
              name: 'aa'
            },
            {
              key: 'a1',
              name: 'a1'
            },
            {
              key: 'a2',
              name: 'a2'
            }
          ]
        }
      ],

      // 
      mainFunctionPage: [
        {
          label: ' 1',
          children: [
            {
              label: ' 1-1',
              children: [
                {
                  label: ' 1-1-1'
                }
              ]
            }
          ]
        },
        {
          label: ' 2',
          children: [
            {
              label: ' 2-1',
              children: [
                {
                  label: ' 2-1-1'
                }
              ]
            },
            {
              label: ' 2-2',
              children: [
                {
                  label: ' 2-2-1'
                }
              ]
            }
          ]
        }
      ],
      defaultMainPageProps: {
        children: 'children',
        label: 'label'
      },
      //
      definePageList: [
        {
          id: '111',
          name: '1',
          src: ''
        },
        {
          id: '112',
          name: '2',
          src: ''
        },
        {
          id: '113',
          name: '3',
          src: ''
        },
        {
          id: '114',
          name: '4',
          src: ''
        },
        {
          id: '115',
          name: '5',
          src: ''
        },
        {
          id: '116',
          name: '6',
          src: ''
        },
        {
          id: '117',
          name: '7',
          src: ''
        },
        {
          id: '118',
          name: '8',
          src: ''
        },
        {
          id: '119',
          name: '9',
          src: ''
        }
      ]
    };
  },
  methods: {
    // --------------------------------------------------
    // 
    linksTypeChange: function(e) {
      log(e);
    },
    // twoLevelValueChange
    twoLevelValueChange(e) {
      log(e);
    },
    // threeLevelValueChange
    threeLevelValueChange(e) {
      log(e);
    },

    // 
    cancelLinkSelect() {
      var that = this;
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: ''
      })
        .then(() => {
          log('');
          that.$emit('linkSelect');
        })
        .catch(() => {
          log(' ');
        });
    },

    // 
    confirmLinkSelect() {
      var that = this;
      log(that.linksType, that.twoLevelValue, that.threeLevelValue);
      // ,
      that.$emit('linkSelect');
    },

    // --------------------------------------------------
    handleClick(tab, event) {
      log(tab, event);
      var that = this;
      switch (that.activeName) {
        case 'first':
          that.selectType = 1;
          break;
        case 'second':
          that.selectType = 2;
          break;
        case 'third':
          that.selectType = 3;
          break;
        case 'fourth':
          that.selectType = 3;
          break;
      }

      log(that.selectType);
    },

    // -------------------------------------------
    //

    // categorySelect
    categorySelect(node, id) {
      var that = this;
      log(node, id);
      // 
      that.getGoodsList();
    },
    // classifySelect
    classifySelect(node, id) {
      var that = this;
      log(node, id);
      // 
      that.getGoodsList();
    },

    //  goodsCategorySelect
    goodsCategorySelect(node, id) {
      var that = this;
      log(node, id);
      //  / 
      that.selectLinkItem();
    },

    // --------------------------------------------

    // 
    handleClose(done) {
      var that = this;
      that
        .$confirm('')
        .then(_ => {
          done();
          that.mylinkToolsVisible = false;
          log(this.mylinkToolsVisible);
          that.$emit('linkSelect');
        })
        .catch(_ => {});
    },

    // 
    linkSizeChange(val) {
      var that = this;
      log(` ${val} `, that.pageSize);
      that.pageSize = val;
    },

    // 
    linkCurrentChange(val) {
      var that = this;
      log(`: ${val}`, that.currentPage);
      that.currentPage = val;
    },

    // ,
    selectLinkItem(item) {
      var that = this;
      log(item);
      // //
      var selectTypeName = null;
      switch (that.selectType) {
        case 1:
          selectTypeName = '';
          break;
        case 2:
          selectTypeName = '';
          break;
        case 3:
          selectTypeName = '';
          break;
        case 4:
          selectTypeName = '';
          break;
      }

      log(that.selectType, selectTypeName);
      that
        .$confirm('' + selectTypeName, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: ''
        })
        .then(() => {
          // that.$emit('linkSelect')
          this.$message({
            type: 'success',
            message: ''
          });
        })
        .catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: ''
          // });
        });
      // that.$emit('linkSelect')
    },

    // 
    toSearchInput: function(value) {
      var that = this;
      log(that.searchGoods, value);
      //   1

      if (that.currentPage == 1) {
        that.getGoodsList();
      } else {
        that.currentPage = 1;
      }
    },
    // 
    clearInput: function(e) {
      var that = this;
      log(e, that.searchGoods);
      //   1

      if (that.currentPage == 1) {
        that.getGoodsList();
      } else {
        that.currentPage = 1;
      }
    },

    // 
    getGoodsList() {
      // var para = {
      //   curPage: that.currentPage,
      //   search: that.searchGoods || '',
      //   pageSize: that.pageSize
      // };
    },

    // 
    mainPageNodeClick: function(data) {
      var that = this;
      log(data);
      // 
      that.selectLinkItem();
    },

    // 
    definePageClick(data) {
      var that = this;
      log(data);
      // 
      that.selectLinkItem();
    }
  },

  watch: {
    linkToolsVisible(val) {
      log(val);
      this.mylinkToolsVisible = val; //linkToolsVisiblewatchlinkToolsVisible
    }
  },

  mounted() {
    log('00000');
  },

  components: {
    Treeselect
  }
};
</script>

<style lang="scss" scoped>
.el-tab-pane {
  min-height: 200px;
}

.vue-treeselect {
  display: inline-block;
}

.inline-block {
  display: inline-block;
}

.goods-link-content {
  padding: 15px 0 20px 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  .goods-link-list {
    font-size: 0;
    li:nth-child(3n + 3) {
      margin-right: 0;
    }
  }
}
.goods-link-item {
  display: inline-block;
  width: 310px;
  padding: 0 10px;
  height: 110px;
  border: 1px solid #e7e7eb;

  margin-right: 7px;
  margin-bottom: 10px;
  cursor: pointer;
}
.goods-link-item img {
  width: 80px;
  height: 80px;
  display: inline-block;
  margin-top: 10px;
}

.goods-message {
  width: 65%;
  margin-top: 10px;
  vertical-align: top;
  margin-left: 5px;
  white-space: normal;
  word-break: break-all;
}

.limit-2 {
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  white-space: pre-wrap;
  word-break: break-all;
  overflow: hidden;
}

.links-tools-row {
  /*overflow: hidden;*/
}

/*  */
.links-tools-page {
  text-align: right;
}

.goods-category {
  height: 300px;
  overflow: auto;
}

/*  */
.main-function-page {
  height: 300px;
  overflow: auto;
}

/*  */
.definepage {
  height: 300px;
  overflow: auto;
  .definepage-ul {
    li {
      height: 50px;
      line-height: 50px;
      cursor: pointer;
      &:hover {
        background: #f5f5f8;
      }
    }
  }
}

/************************************************/
.links-types {
  .types-title {
    display: inline-block;
    width: 80px;
    text-align: right;
  }
}

/*  */
.goods-links-contian {
  margin-top: 10px;
}

/* h5 */
.h5link-row {
  width: 100%;
  margin-top: 10px;

  label {
    display: inline-block;
    width: 80px;
    text-align: right;
  }

  .el-input,
  .el-textarea {
    width: 450px;
  }

  .el-textarea {
    vertical-align: top;
  }
}
</style>



